import React from 'react';

import { Flex, Text, Tabs, Button } from '@mantine/core';
import { TabType } from './content';


interface HeaderProps {
  activeTab: string;
  setActiveTab: (value: string) => void;
  tabs: Array<TabType>;
}

export function Header({activeTab, setActiveTab, tabs}: HeaderProps) {

  const resume = './static/Resume_Andrew_Bezold_20231128.pdf'

  const handleTabChange = (value: string | null) => {
    setActiveTab(value === null ? "about" : value);
  }

  return (
    <Flex justify="space-between" bg="var(--mantine-color-dark-5)" h="36px" pl="sm" pr="sm">
      <Flex align="center">
        <Text>
          This is a header
        </Text>
      </Flex>
      <Flex gap="md">
        <Tabs value={activeTab} onChange={handleTabChange}>
          <Tabs.List>
            {tabs.map((tab) => 
              <Tabs.Tab value={tab.id}>
                {tab.name}
              </Tabs.Tab>
            )}
          </Tabs.List>
        </Tabs>
        <Flex gap="sm">
          <Button component="a" download="Andrew_Bezold_Resume.pdf" href={resume}>
            Download Resume
          </Button>
        </Flex>
      </Flex>
      <div />
    </Flex>
  )
}