import React from 'react';
import { Flex, Text, Title } from '@mantine/core';
import WorkExperience from '../static/work_experience.json';



interface JobProps {
    company: string,
    title: string,
    dates: string,
    experience: Array<string>,
}

function Job({ company, title, dates, experience }: JobProps) {
    return (
        <Flex direction="column">
            <Flex direction="column">
                <Flex justify="space-between" align="end" w="400px">
                    <Title order={3}>
                        {company}
                    </Title>
                    <Text>
                        {dates}
                    </Text>
                </Flex>
                <Text>
                    {title}
                </Text>
            </Flex>
            <Flex direction="column" mt="sm">
                {experience.map((line) => 
                    <Text pl="xl">
                        - {line}
                    </Text>
                )}
            </Flex>
        </Flex>
    )
}

export function WorkTab() {
    return (
        <Flex direction="column" gap="xl">
            <Flex direction="column">
                <Title>
                    Education
                </Title>
                <Flex pl="lg" direction="column">
                    <Text>
                        University of Louisville, 2015
                    </Text>
                    <Text>
                        B.S. Computer Engineering/Computer Science
                    </Text>
                </Flex>
            </Flex>
            <Flex direction="column">
                <Title>
                    Skills
                </Title>
                <Text pl="lg">
                    Python, Django, Javascript, Typescript, React, Kotlin, PostgreSQL, Git, add more here
                </Text>
            </Flex>
            <Flex direction="column" gap="md">
                {WorkExperience.map((job) => 
                    <Job
                        company={job.company}
                        title={job.title}
                        dates={job.dates}
                        experience={job.experience}
                    />
                )}
            </Flex>
        </Flex>
    )
}